@import "src/common/styles/variables";
@import "src/common/styles/Templates.module";


.skillsContent {
  @extend %tplContent;
  flex-direction: column;

  .skillsItems {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: flex-start;
  }
  .personalInfoBlock {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 10px 20px;
    width: 100%;
    .photoBlock {
      width: 300px;
      position: relative;
      > img {
        background-size: cover;
        background-position: center;
        width: 100%;
        height: auto;
        border-radius: 30px;
        box-shadow: 0 0 10px RGB(0 0 0 / 90%);
      }
    }
    .infoBlock {
      width: 300px;
      > h3 {
        font-size: 26px;
        font-weight: 700;
        line-height: 120%;
        letter-spacing: 2px;
        text-transform: capitalize;
        color: #fff;
        padding: 5px 0 15px;
        > span {
          font-size: 26px;
          color: $blue;
          margin-left: 7px;
        }
      }
      .items {
        margin-top: 10px;
        font-size: 16px;
        list-style: none;
        color: #fff;
        display: flex;
        flex-direction: column;
        justify-content: end;
        align-items: start;
        > li {
          color: $text;
          padding-bottom: 10px;
          letter-spacing: 1px;
          > span {
            color: $white;
            margin-left: 8px;
          }
        }
      }
    }
    .contacts {
      width: 200px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .icons {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: 30px;
        margin-bottom: 40px;
        .iconLink {
          color: $white;
          margin: 0 15px;
          transition: transform 1s ease-in-out;
          &:hover {
            color: $blue;
            transform: translateY(-8px);
          }
        }
      }
    }
  }
}


@media (min-width: 350px) and (max-width: 390px){
  .skillsContent {
    .personalInfoBlock {
      .contacts {
        .icons {
          .iconLink {
            color: $blue;
          }
        }
      }
    }
  }
}